import React from "react";
import ReactToPdf from "react-to-pdf";

function PDF(props){
    const ref = React.createRef();
    const duomenys = props.data;
    var imone;
    if(duomenys.company.slice(0,4) === "com1"){imone = "Imon1"}
    if(duomenys.company.slice(0,4) === "com2"){imone = "Imon2"}
    if(duomenys.company.slice(0,4) === "com3"){imone = "Imon3"}
    if(duomenys.type === "Kasmetins atostogos"){duomenys.type = "kasmetini atostog"};
    if(duomenys.type === "Dekretins atostogos"){duomenys.type = "dekretini atostog"};
    if(duomenys.type === "Tvysts atostogos"){duomenys.type = "tvysts atostog"};
    if(duomenys.type === "Vaiko priiros atostogos"){duomenys.type = "vaiko prieiros atostog"};
    if(duomenys.type === "Mokymosi atostogos"){duomenys.type = "mokymosi atostog"};
    if(duomenys.type === "Krybins atostogos"){duomenys.type = "krybini atostog"};
    if(duomenys.type === "Nemokamos atostogos"){duomenys.type = "nemokam atostog"};
    return(
        <div class="pdf-page">
            <ReactToPdf targetRef={ref} filename={"Atostog praymas " + duomenys.name + ".pdf"}>
                {({toPdf}) => (
                    <button class="page-buttons" onClick={toPdf}>Atsisisti PDF</button>
                )}
            </ReactToPdf>
            <div style={{paddingBottom: "100px", paddingRight: "80px", width: 793.706, height: 1122.52}}>
                <div style={{width: 793.706, height: 1122.52, background: 'white'}} ref={ref}>
                    <div style={{width: 642.536, height: 920.96, padding: "100.78px 50.39px 100.78px 100.78px", display: "flex", flexDirection: "column", alignItems: "center"}}>
                        <div style={{fontSize: "15.9px", fontWeight: "bold"}}>Darbuotojas</div>
                        <div style={{fontSize: "18.6px", fontWeight: "bold", textTransform: "uppercase", paddingBottom: "50px"}}>{duomenys.name}</div>
                        <div style={{fontSize: "16px", textTransform: "uppercase", textIndent: "-321.268px"}}>{"UAB " + imone}</div>
                        <div style={{fontSize: "16px", paddingBottom: "80px", textIndent: "-321.268px"}}>{"Vadovas " + duomenys.confirmedBy}</div>
                        <div style={{fontSize: "18.6px", fontWeight: "bold", textTransform: "uppercase", paddingBottom: "40px"}}>{"Praymas dl " + duomenys.type}</div>
                        <div style={{fontSize: "15.9px"}}>{duomenys.date}</div>
                        <div style={{fontSize: "15.9px", paddingBottom: "70px"}}>{"Kaunas"}</div>
                        <div style={{fontSize: "15.9px", textIndent: "40px", paddingBottom: "150px"}}>{"Praau mane leisti " + duomenys.type + " nuo " + duomenys.startdate + " iki " + duomenys.enddate + ". Praau man priklausanius atostoginius imokti kartu su darbo umokesio mokjimu."}</div>
                        <div style={{fontSize: "15.9px", textIndent: "420px"}}>{duomenys.name}</div>
                        <div style={{fontSize: "13.3px"}}>(paraas)</div>
                    </div>
                </div>
            </div>
            
        </div>
    )
}
export default PDF;